<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="create-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="问题提报" left-arrow @click-left="onClickLeft"></van-nav-bar>

    <van-cell-group>
      <van-cell required title="故障地点" is-link clickable @click="handleSelectLine">
        <span>{{ line && line.name }} / {{ station && station.name }}</span>
      </van-cell>
      <van-field v-model="address" placeholder="请输入详细地址" type="textarea" :rows="2" autosize />
      <van-cell required title="发生时间" is-link clickable @click="handleSelectTime">
        <span>{{ time }}</span>
      </van-cell>
    </van-cell-group>

    <van-cell-group>
      <van-cell required title="所属专业" is-link clickable @click="handleSelectProfession">
        <span>{{ profession && profession.name }}</span>
      </van-cell>
      <van-cell required title="故障设备分类" is-link clickable @click="handleSelectDeviceCategory">
        <span>{{ deviceCategory && deviceCategory.name }}</span>
      </van-cell>
      <van-field v-model="carNo" label="车组号" placeholder="请输入车组号" :disabled="!(profession && (profession.name == '通信专业' || profession.name == '信号专业' ))" />
      <van-cell required title="问题分类" is-link clickable @click="handleSelectCategory">
        <span>{{ category && category.name }}</span>
      </van-cell>
      <van-cell required title="问题等级" is-link clickable @click="handleSelectLevel">
        <span>{{ level && level.name }}</span>
      </van-cell>
      <van-cell required title="问题来源" is-link clickable @click="handleSelectSource">
        <span>{{ source && source.name }}</span>
      </van-cell>
      <van-cell required title="计划完成时间" is-link clickable @click="handleSelectFinishTime">
        <span>{{ finishTime }}</span>
      </van-cell>
      <van-cell title="承包商" is-link clickable @click="handleSelectContractor">
        <span>{{ contractor && contractor.name }}</span>
      </van-cell>
      <van-cell title="通知承包商时间" is-link clickable @click="handleSelectNoticeTime">
        <span>{{ contractorNoticeTime ? contractorNoticeTime : '请选择' }}</span>
      </van-cell>
      <van-cell title="承包商答复时间" is-link clickable @click="handleSelectReplyTime">
        <span>{{ contractorReplyTime ? contractorReplyTime : '请选择' }}</span>
      </van-cell>
      <van-cell title="承包商开始工作时间" is-link clickable @click="handleSelectBeginTime">
        <span>{{ contractorBeginTime ? contractorBeginTime : '请选择' }}</span>
      </van-cell>
      <van-cell title="承包商完成工作时间" is-link clickable @click="handleSelectContractorFinishTime">
        <span>{{ contractorFinishTime ? contractorFinishTime : '请选择' }}</span>
      </van-cell>
      <van-field v-model="noRepairTime" label="非维修时间：" placeholder="请输入非维修时间"  />
      <van-cell title="故障修复时间" is-link clickable @click="handleSelectRepairTime">
        <span>{{ repairFinishTime ? repairFinishTime : '请选择' }}</span>
      </van-cell>
      <!-- contractor -->
    </van-cell-group>

    <!-- <van-cell-group>
      <van-cell required class="detail-title" title="问题摘要："></van-cell>
      <van-field v-model="summary" placeholder="请填写问题摘要" type="textarea" :rows="2" autosize />
    </van-cell-group> -->

    <van-cell-group>
      <van-cell class="detail-title" title="问题简述："></van-cell>
      <van-field v-model="problemDesc" placeholder="请填写问题简述" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell required class="detail-title" title="现象描述："></van-cell>
      <van-field v-model="description2" placeholder="请填写现象描述" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell required class="detail-title" title="初步原因分析："></van-cell>
      <van-field v-model="analysis" placeholder="请填写初步原因分析" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell required class="detail-title" title="初步解决方案："></van-cell>
      <van-field v-model="beginSolutions" placeholder="请填写初步解决方案" type="textarea" :rows="2" autosize />
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="图像："></van-cell>
      <div class="images-container">
        <div class="image-item-show" v-for="(item, index) in imageList" :key="index" @click="handlePlayImages(index)">
          <!-- <img :src="item.url" @click="handlePlayImages(index)"> -->
          <auto-image
            :src="item.url"
            :width="108"
            :height="108"
            style="margin-bottom: 12px;margin-left: 12px;"
          ></auto-image>
          <div class="image-remove" @click.stop="handleImageRemove(index)"></div>
        </div>
        <div class="image-item" style="background: #fff;" @click.stop="handlePhoto1">
          <img src="../../assets/images/image-add.png" />
        </div>
      </div>
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="视频："></van-cell>
      <div class="images-container">
        <div class="image-item-show" v-for="(item, index) in videoList" :key="index" @click="handlePlayVideo(item)">
          <!-- <auto-image :src="item.url" :width="108" :height="108" style="margin-bottom: 12px;margin-left: 12px;"></auto-image> -->
          <img src="../../assets/images/video-default.png" class="img-video" />
          <div class="image-remove" @click.stop="handleVideoRemove(index)"></div>
        </div>
        <div class="image-item" style="background: #fff;" @click.stop="handlePhoto2">
          <img src="../../assets/images/image-add.png" />
        </div>
      </div>
    </van-cell-group>

    <van-cell-group>
      <van-cell class="detail-title" title="附件："></van-cell>
      <div class="images-container">
        <div class="image-item-show" v-for="(item, index) in annexList" :key="index" @click="handlePlayAnnex(item)">
          <!-- <auto-image :src="item.url" :width="108" :height="108" style="margin-bottom: 12px;margin-left: 12px;"></auto-image> -->
          <img src="../../assets/images/annex-default.png" class="img-video" />
          <div class="image-remove" @click.stop="handleAnnexRemove(index)"></div>
        </div>
        <div class="image-item" style="background: #fff;" @click.stop="handlePhoto3">
          <img src="../../assets/images/image-add.png" />
        </div>
      </div>
    </van-cell-group>

    <div class="blank-empty"></div>

    <template slot="blank">
      <div class="footer-action">
        <van-button class="action-item-main"  @click="handleSave">保存</van-button>
        <van-button class="action-item-main" color="#108EE9" @click="handleSubmit">提交</van-button>
      </div>

      <van-popup
        v-model="professionShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择所属专业</div>
        <a-scroll ref="filterScroller" v-if="professionShow" class="filter-scroll">
          <van-radio-group v-model="professionRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="professionRadio = {...item}"
                v-for="(item, index) in professionOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onProfessionSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="sourceShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择问题来源</div>
        <a-scroll ref="filterScroller" v-if="sourceShow" class="filter-scroll">
          <van-radio-group v-model="sourceRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="sourceRadio = {...item}"
                v-for="(item, index) in sourceOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onSourceSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="categoryShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择问题分类</div>
        <a-scroll ref="filterScroller" v-if="categoryShow" class="filter-scroll">
          <van-radio-group v-model="categoryRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="categoryRadio = {...item}"
                v-for="(item, index) in categoryOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onCategorySelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="contractorShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择承包商</div>
        <a-scroll ref="filterScroller" v-if="contractorShow" class="filter-scroll">
          <van-radio-group v-model="contractorRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="contractorRadio = {...item}"
                v-for="(item, index) in contractorOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onContractorSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="levelShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">选择问题等级</div>
        <a-scroll ref="filterScroller" v-if="levelShow" class="filter-scroll">
          <van-radio-group v-model="levelRadio.id">
            <van-cell-group>
              <van-cell
                :title="item.name"
                :label="item.description"
                clickable
                @click="levelRadio = {...item}"
                v-for="(item, index) in levelOptions"
                :key="index"
              >
                <van-radio slot="right-icon" :name="item.id" />
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="onLevelSelect">确定</van-button>
        </div>
      </van-popup>

      <van-popup v-model="timeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          v-model="currentTime"
          type="datetime"
          @confirm="handleConfirm"
          @cancel="timeShow = false"
        />
      </van-popup>

      <van-popup v-model="finishTimeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          v-model="currentTime"
          type="datetime"
          @confirm="handleFinishTimeConfirm"
          @cancel="finishTimeShow = false"
        />
      </van-popup>

      <van-popup v-model="contractorReplyTimeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          type="datetime"
          v-model="createTime"
          @confirm="handleReplyTimeConfirm"
          @cancel="contractorReplyTimeShow = false"
        />
      </van-popup>

      <van-popup v-model="contractorBeginTimeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          type="datetime"
          v-model="createTime"
          @confirm="handleBeginTimeConfirm"
          @cancel="contractorBeginTimeShow = false"
        />
      </van-popup>

      <van-popup v-model="contractorFinishTimeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          type="datetime"
          v-model="createTime"
          @confirm="handleContractorFinishTimeConfirm"
          @cancel="contractorFinishTimeShow = false"
        />
      </van-popup>

      <van-popup v-model="repairFinishTimeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          type="datetime"
          v-model="createTime"
          @confirm="handleRepairTimeConfirm"
          @cancel="repairFinishTimeShow = false"
        />
      </van-popup>

      <van-popup v-model="contractorNoticeTimeShow" position="bottom" class="time-popup" :lock-scroll="false" round>
        <van-datetime-picker
          type="datetime"
          v-model="createTime"
          @confirm="handleNoticeTimeConfirm"
          @cancel="contractorNoticeTimeShow = false"
        />
      </van-popup>

      <van-dialog v-model="editShow" show-cancel-button :before-close="beforeClose">
        <van-cell title="描述"></van-cell>
        <van-field ref="desInput" v-model="description" type="textarea" :clearable="true" />
      </van-dialog>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import AScroll from '../../components/Scroll'
import navigate from '../../utils/navigate.js'
import AutoImage from '../../components/AutoImage'
import db from '../../db'
import io from '../../api'
import moment from 'moment'
import native from '../../utils/native.js'
import { uploadImage, uploadVideo, uploadAnnex} from '../../utils/upload.js'

export default {
  components: {
    Page,
    AScroll,
    AutoImage
  },
  data: () => ({
    headerHeightVW: 0,
    levelOptions: JSON.parse(localStorage.getItem('problem_level')),
    categoryOptions: JSON.parse(localStorage.getItem('problem_category')),
    professionOptions: JSON.parse(localStorage.getItem('problem_profession')),
    sourceOptions: JSON.parse(localStorage.getItem('problem_source')),
    contractorOptions: JSON.parse(localStorage.getItem('problem_contractor')),
    profession: null,
    source: null,
    level: null,
    device: null,
    category: null,
    deviceCategory: null,
    reason: null,
    contractor: null,
    reasonShow: false,
    reasonRadio: null,
    sourceShow: false,
    sourceRadio: null,
    categoryShow: false,
    categoryRadio: null,
    levelShow: false,
    levelRadio: null,
    professionShow: false,
    professionRadio: null,
    contractorShow: false,
    contractorRadio: null,
    imageList: [],
    videoList: [],
    annexList: [],
    UIPhotoViewer: null,
    editShow: false,
    description: '',
    description2: '',
    problemDesc: '',
    imgFile: '',
    repairContent: '',
    repairRemark: '',
    currentTime: new Date(),
    timeShow: false,
    time: moment().format('YYYY-MM-DD HH:mm'),
    line: null,
    station: null,
    lineShow: false,
    lineColumns: [],
    address: '',
    content: '',
    finishTimeShow: false,
    finishTime: moment().format('YYYY-MM-DD HH:mm'),
    summary: '',
    description: '',
    analysis: '',
    beginSolutions: '',
    localId: null,
    imageFilter: null,
    videoCompression: null,
    carNo: '',

    contractorNoticeTime: '',
    contractorNoticeTimeShow: false,

    contractorReplyTime: '',
    contractorReplyTimeShow: false,

    contractorBeginTime: '',
    contractorBeginTimeShow: false,

    contractorFinishTime: '',
    contractorFinishTimeShow: false,

    noRepairTime: '',
    noRepairTimeShow: false,

    repairFinishTime: '',
    repairFinishTimeShow: false,

    fileBrowser: null,
    fileReader: null
  }),
  methods: {
    onReady(header, main) {
      this.header = header

      this.headerHeightVW = ((this.header.h + 1) * 100) / this.header.w

      localStorage.setItem('address_select', '')

      localStorage.setItem('category_select', '')

      const detailId = navigate.getParam('detail')

      if (detailId) {
        db.getMyProblemDetail(detailId).then(data => {
          // alert(JSON.stringify(data))

          this.profession = data.profession
          this.time = data.time
          this.line = data.line
          this.station = data.station
          this.address = data.address
          this.category = data.category
          this.level = data.level
          this.source = data.source
          this.imageList = data.images
          this.videoList = data.videos
          this.annexList = data.annex
          this.finishTime = data.finishTime
          this.summary = data.summary
          this.description2 = data.description
          this.problemDesc = data.problemDesc
          this.analysis = data.analysis
          this.beginSolutions = data.beginSolutions
          this.localId = data.id,
          this.deviceCategory = data.deviceCategory
          this.carNo = data.carNo
          this.contractor = data.contractor,
          this.contractorNoticeTime = data.contractorNoticeTime,
          this.contractorReplyTime = data.contractorReplyTime,
          this.contractorBeginTime = data.contractorBeginTime,
          this.contractorFinishTime = data.contractorFinishTime
          this.noRepairTime = data.noRepairTime
          this.repairFinishTime = data.repairFinishTime
        })
      }
    },

    onShow() {
      if (localStorage.getItem('address_select')) {
        const address = JSON.parse(localStorage.getItem('address_select'))

        this.line = address[0]

        this.station = address[address.length - 1]
      }

      if (localStorage.getItem('category_select')) {
        const category = JSON.parse(localStorage.getItem('category_select'))

        this.deviceCategory = category[category.length - 1]
      }
    },

    onClickLeft() {
      this.$dialog.confirm({
        message: '返回数据将丢失，是否确认返回？',
        confirmButtonText: '确认',
        cancelButtonText: '取消'
      }).then(() => {
        navigate.back()
      }).catch(() => {})
    },

    handleSelectTime() {
      this.timeShow = true
    },

    handleSelectFinishTime() {
      this.finishTimeShow = true
    },

    handleSelectLine() {
      navigate.open('address')
    },

    handleSelectDeviceCategory () {
      navigate.open('deviceCategory', {profession: this.profession ? this.profession.id : null})
    },

    handleConfirm(time) {
      this.time = moment(time).format('YYYY-MM-DD HH:mm')
      this.timeShow = false
    },

    handleFinishTimeConfirm(time) {
      this.finishTime = moment(time).format('YYYY-MM-DD HH:mm')
      this.finishTimeShow = false
    },

    handleSelectSource() {
      this.sourceShow = true
      this.sourceRadio = { ...this.reason }
    },

    onSourceSelect() {
      this.source = { ...this.sourceRadio }
      this.sourceShow = false
    },

    handleSelectCategory() {
      this.categoryShow = true
      this.categoryOptions = JSON.parse(localStorage.getItem('problem_category')).filter(
        item => item.profession && item.profession.findIndex(p => this.profession && p == this.profession.id) >= 0
      )
      this.categoryRadio = { ...this.category }
    },

    onCategorySelect() {
      this.category = { ...this.categoryRadio }
      this.categoryShow = false
    },

    handleSelectLevel() {
      this.levelShow = true
      this.levelOptions = JSON.parse(localStorage.getItem('problem_level')).filter(
        item => item.profession.findIndex(p => this.profession && p == this.profession.id) >= 0
      )
      this.levelRadio = { ...this.level }
    },

    onLevelSelect() {
      this.level = { ...this.levelRadio }
      this.levelShow = false
    },

    handleSelectProfession() {
      this.professionShow = true
      this.professionRadio = { ...this.profession }
    },

    onProfessionSelect() {
      this.profession = { ...this.professionRadio }
      this.professionShow = false
    },

    handleSelectContractor () {
      this.contractorShow = true
      this.contractorRadio = {...this.contractor}
    },

    onContractorSelect () {
      this.contractor = {...this.contractorRadio}
      this.contractorShow = false
    },

    handleReplyTimeConfirm(time) {
      this.contractorReplyTime = moment(time).format('YYYY-MM-DD HH:mm')
      this.contractorReplyTimeShow = false
    },

    handleSelectReplyTime () {
      this.createTime = this.contractorReplyTime ? new Date(this.contractorReplyTime) : new Date()
      this.contractorReplyTimeShow = true
    },

    handleSelectBeginTime () {
      this.createTime = this.contractorBeginTime ? new Date(this.contractorBeginTime) : new Date()
      this.contractorBeginTimeShow = true
    },

    handleBeginTimeConfirm (time) {
      this.contractorBeginTime = moment(time).format('YYYY-MM-DD HH:mm')
      this.contractorBeginTimeShow = false
    },

    handleSelectContractorFinishTime () {
      this.createTime = this.contractorFinishTime ? new Date(this.contractorFinishTime) : new Date()
      this.contractorFinishTimeShow = true
    },

    handleContractorFinishTimeConfirm (time) {
      this.contractorFinishTime = moment(time).format('YYYY-MM-DD HH:mm')
      this.contractorFinishTimeShow = false
    },

    handleSelectRepairTime () {
      this.createTime = this.repairFinishTime ? new Date(this.repairFinishTime) : new Date()
      this.repairFinishTimeShow = true
    },

    handleRepairTimeConfirm (time) {
      this.repairFinishTime = moment(time).format('YYYY-MM-DD HH:mm')
      this.repairFinishTimeShow = false
    },

    handleSelectNoticeTime () {
      this.createTime = this.contractorNoticeTime ? new Date(this.contractorNoticeTime) : new Date()
      this.contractorNoticeTimeShow = true
    },

    handleNoticeTimeConfirm (time) {
      this.contractorNoticeTime = moment(time).format('YYYY-MM-DD HH:mm')
      this.contractorNoticeTimeShow = false
    },

    handlePhoto1() {
      api.actionSheet({
        buttons: ['打开照相机', '从相册获取']
      }, (ret, err) => {
        var index = ret.buttonIndex;
        let sourceType = ''
        if (index === 1) {
          sourceType = 'camera'
        } else if (index === 2) {
          sourceType = 'album'
        }

        sourceType && api.getPicture(
          {
            sourceType: sourceType,
            destinationType: 'url',
            preview: true,
            quality: 50,
            mediaValue: 'pic'
          },
          (ret, err) => {
            // alert(JSON.stringify(ret))

            if (ret.data) {
              this.imgFile = ret.data
              this.description = ''
              this.editShow = true
              setTimeout(() => {
                this.$refs.desInput.focus()
              }, 200)
            }
          }
        )
      })
    },

    handlePhoto3() {
      if (api.systemType == 'android') {
        if (!this.fileBrowser) {
          this.fileBrowser = api.require('selectFile')
        }

        this.fileBrowser.open((ret) => {
          if(ret.status){
            this.annexList.push({url: ret.path})
          }else{
            alert('选择文件不存在');
          }
        })
      }

      if (api.systemType == 'ios') {
        if (!this.fileReader) {
          this.fileReader = api.require('fileReader')
        }

        this.fileReader.open((ret) => {
          if (ret.status) {
            this.fileReader.addEventListener({
              target: 'select'
            }, (ret) =>{
              // alert(JSON.stringify(ret.urls))
              if (ret.urls.length) {
                this.annexList.push({url: ret.urls[0]})
              }
            })
          }
        })
      }
    },

    handlePhoto2() {
      api.actionSheet({
        buttons: ['打开照相机', '从相册获取']
      }, (ret, err) => {
        var index = ret.buttonIndex;
        let sourceType = ''
        if (index === 1) {
          sourceType = 'camera'
        } else if (index === 2) {
          sourceType = 'album'
        }
        sourceType && api.getPicture(
          {
            sourceType: sourceType,
            destinationType: 'url',
            preview: true,
            quality: 100,
            mediaValue: 'video',
            saveToPhotoAlbum: true,
            videoQuality: 'high',
          },
          (ret, err) => {
            if (ret.data) {
              // this.videoList.push({ url: ret.data })
              if (api.systemType == 'ios') {
                this.$toast.loading({
                  duration: 0,
                  forbidClick: true,
                  loadingType: 'spinner',
                  message: '正在压缩...'
                })

                if (!this.videoCompression) {
                  this.videoCompression = api.require('videoCompression')
                }

                this.videoCompression.compression({
                  path: ret.data,
                  quality: 'medium'
                },(ret1, err1) => {
                  // alert(JSON.stringify(ret1))
                  if (ret1.eventType == 'completed') {
                    this.videoList.push({ url: ret1.path })
                    this.$toast.clear()
                  }
                })
              } else {
                this.videoList.push({ url: ret.data })
              }
            }
          }
        )
      })
    },

    beforeClose(action, done) {
      if (action === 'confirm') {
        this.imageList.push({
          url: this.imgFile,
          description: this.description
        })
      } else {
        this.imageList.push({ url: this.imgFile, description: '' })
      }
      done()
    },

    handleVideoRemove(index) {
      this.videoList.splice(index, 1)
    },

    handleImageRemove(index) {
      this.imageList.splice(index, 1)
    },

    handlePlayVideo(item) {
      api.openVideo({
        url: item.url
      })
    },

    handlePlayAnnex(item) {
      navigate.open('browser', {url: item.url})
    },

    handleAnnexRemove(index) {
      this.annexList.splice(index, 1)
    },

    handlePlayImages(index) {
      localStorage.setItem('photos', JSON.stringify(this.imageList))
      navigate.open('photoViewer', { index: index })
    },

    handleSubmit() {
      if (
        this.line == null ||
        this.station == null ||
        this.time == '' ||
        this.source == null ||
        // this.summary == '' ||
        this.description2 == '' ||
        this.analysis == '' ||
        this.beginSolutions == '' ||
        this.profession == null ||
        this.category == null ||
        this.level == null ||
        this.finishTime == '' ||
        this.deviceCategory == null 
      ) {
        this.$toast.fail('请填写所有必填项,可填写无')
      } else {
        this.$toast.loading({
          duration: 0,
          forbidClick: true,
          loadingType: 'spinner',
          message: '正在提交...'
        })

        if (native.connection()) {
          uploadImage(this.imageList).then(s => {
            this.imageList = s

            uploadVideo(this.videoList).then(v => {
              this.videoList = v

              uploadAnnex(this.annexList).then(a => {
                this.annexList = a

                io.createProblem({
                  profession: this.profession ? this.profession.id : '',
                  time: this.time,
                  line: this.line ? this.line.id : '',
                  station: this.station ? this.station.id : '',
                  address: this.address,
                  category: this.category ? this.category.id : '',
                  level: this.level ? this.level.id : '',
                  source: this.source ? this.source.id : '',
                  images: JSON.stringify(this.imageList),
                  videos: JSON.stringify(this.videoList),
                  annex: JSON.stringify(this.annexList),
                  finishTime: this.finishTime,
                  summary: this.summary,
                  description: this.description2,
                  problemDesc: this.problemDesc,
                  analysis: this.analysis,
                  beginSolutions: this.beginSolutions,
                  deviceCategory: this.deviceCategory ? this.deviceCategory.id : '',
                  carNo: this.carNo,
                  contractor: this.contractor ? this.contractor.id : '',
                  contractorNoticeTime: this.contractorNoticeTime ? this.contractorNoticeTime.substr(0, 16) + ':00' : '',
                  contractorReplyTime: this.contractorReplyTime ? this.contractorReplyTime.substr(0, 16) + ':00' : '',
                  contractorBeginTime: this.contractorBeginTime ? this.contractorBeginTime.substr(0, 16) + ':00' : '',
                  contractorFinishTime: this.contractorFinishTime ? this.contractorFinishTime.substr(0, 16) + ':00' : '',
                  noRepairTime: this.noRepairTime,
                  repairFinishTime: this.repairFinishTime ? this.repairFinishTime.substr(0, 16) + ':00' : ''
                })
                  .then(data => {
                    if (this.localId) {
                      db.removeMyProblem(this.localId).then(() => {
                        // navigate.open('my_question')
                        navigate.back()
                      })
                    } else {
                      navigate.open('my_question')
                      // navigate.back()
                    }
                  })
                  .catch(msg => {
                    this.$toast(msg)
                  })
              })
            })
          })
        } else {
          if (this.localId) {
            db.updateMyProblem({
              profession: this.profession,
              time: this.time,
              line: this.line,
              station: this.station,
              address: this.address,
              category: this.category,
              level: this.level,
              source: this.source,
              images: this.imageList,
              videos: this.videoList,
              annex: this.annexList,
              finishTime: this.finishTime,
              summary: this.summary,
              description: this.description2,
              problemDesc: this.problemDesc,
              analysis: this.analysis,
              beginSolutions: this.beginSolutions,
              id: this.localId,
              deviceCategory: this.deviceCategory,
              carNo: this.carNo,
              contractor: this.contractor,
              contractorNoticeTime: this.contractorNoticeTime,
              contractorReplyTime: this.contractorReplyTime,
              contractorBeginTime: this.contractorBeginTime,
              contractorFinishTime: this.contractorFinishTime,
              noRepairTime: this.noRepairTime,
              repairFinishTime: this.repairFinishTime
            })
              .then(() => {
                alert('无法连接到网络，数据已保存，可在【我提报的问题】中找到并提交数据')
                // navigate.open('my_question')
                navigate.back()
              })
              .catch(msg => {
                this.$toast(msg)
              })
          } else {
            db.saveMyProblem({
              profession: this.profession,
              time: this.time,
              line: this.line,
              station: this.station,
              address: this.address,
              category: this.category,
              level: this.level,
              source: this.source,
              images: this.imageList,
              videos: this.videoList,
              annex: this.annexList,
              finishTime: this.finishTime,
              summary: this.summary,
              description: this.description2,
              problemDesc: this.problemDesc,
              analysis: this.analysis,
              beginSolutions: this.beginSolutions,
              id: new Date().getTime(),
              deviceCategory: this.deviceCategory,
              carNo: this.carNo,
              contractor: this.contractor,
              contractorNoticeTime: this.contractorNoticeTime,
              contractorReplyTime: this.contractorReplyTime,
              contractorBeginTime: this.contractorBeginTime,
              contractorFinishTime: this.contractorFinishTime,
              noRepairTime: this.noRepairTime,
              repairFinishTime: this.repairFinishTime
            })
              .then(() => {
                alert('无法连接到网络，数据已保存，可在【我提报的问题】中找到并提交数据')

                navigate.open('my_question')
                // navigate.back()
              })
              .catch(msg => {
                this.$toast(msg)
              })
          }
        }
      }
    },

    handleSave () {
      if (
        this.line == null ||
        this.station == null ||
        this.time == '' ||
        this.source == null ||
        // this.summary == '' ||
        this.description2 == '' ||
        this.analysis == '' ||
        this.beginSolutions == '' ||
        this.profession == null ||
        this.category == null ||
