        this.deviceCategory == null
        // this.contractor == null
      ) {
        this.$toast.fail('请填写所有必填项,可填写无')
      } else {

        if (this.localId) {
            db.updateMyProblem({
              profession: this.profession,
              time: this.time,
              line: this.line,
              station: this.station,
              address: this.address,
              category: this.category,
              level: this.level,
              source: this.source,
              images: this.imageList,
              videos: this.videoList,
              annex: this.annexList,
              finishTime: this.finishTime,
              summary: this.summary,
              description: this.description2,
              problemDesc: this.problemDesc,
              analysis: this.analysis,
              beginSolutions: this.beginSolutions,
              id: this.localId,
              deviceCategory: this.deviceCategory,
              carNo: this.carNo,
              contractor: this.contractor,
              contractorNoticeTime: this.contractorNoticeTime,
              contractorReplyTime: this.contractorReplyTime,
              contractorBeginTime: this.contractorBeginTime,
              contractorFinishTime: this.contractorFinishTime,
              noRepairTime: this.noRepairTime,
              repairFinishTime: this.repairFinishTime
            })
              .then(() => {
                alert('保存成功')
                // navigate.open('my_question')
                navigate.back()
              })
              .catch(msg => {
                this.$toast(msg)
              })
          } else {
            db.saveMyProblem({
              profession: this.profession,
              time: this.time,
              line: this.line,
              station: this.station,
              address: this.address,
              category: this.category,
              level: this.level,
              source: this.source,
              images: this.imageList,
              videos: this.videoList,
              annex: this.annexList,
              finishTime: this.finishTime,
              summary: this.summary,
              description: this.description2,
              problemDesc: this.problemDesc,
              analysis: this.analysis,
              beginSolutions: this.beginSolutions,
              id: new Date().getTime(),
              deviceCategory: this.deviceCategory,
              carNo: this.carNo,
              contractor: this.contractor,
              contractorNoticeTime: this.contractorNoticeTime,
              contractorReplyTime: this.contractorReplyTime,
              contractorBeginTime: this.contractorBeginTime,
              contractorFinishTime: this.contractorFinishTime,
              noRepairTime: this.noRepairTime,
              repairFinishTime: this.repairFinishTime
            })
              .then(() => {
                alert('保存成功')

                navigate.open('my_question')
                // navigate.back()
              })
              .catch(msg => {
                this.$toast(msg)
              })
          }
      }
    }
  }
}
</script>

